class InputSystem {
    constructor(rect) {
        this.rect = rect;
        this.drag = new Drag(rect);
        this.zoom = new Zoom(rect);
        
        this.init();
    }

    init() {
        
        // mouse events
        this.rect.onmousedown = e => {
            this._mouseDown(e);
            
        }

        this.rect.onmousemove = e => {
            this._mouseMove(e);
        }

        this.rect.onmouseup = () => {
            this._mouseUp();
        }


        //touch events
        this.rect.addEventListener("touchstart" , (e) => {
            this._touchStart(e);
        });

        this.rect.addEventListener("touchmove" , (e) => {
            this._touchMove(e);
        });

        this.rect.addEventListener("touchend" , () => {
            this._touchEnd();
        });

        /**
         * zoom control events
         */
        this.rect.addEventListener('wheel', (e) => {
            e.preventDefault();
            this.zoom._wheelEvent(e);
        });

        this._pinchZoomEvent();

    }

    _pinchZoomEvent(){
        //touch events
        this.rect.addEventListener("pointerdown" , (e) => {
            this.zoom.touchStart(e);
        });

        this.rect.addEventListener("pointermove" , (e) => {
            this.zoom.touchMove(e);
        });

        this.rect.addEventListener("pointerup" , (e) => {
            this.zoom.touchEnd(e);
        });
    }


    


    //#region Mouse interactions
    _mouseDown(e){
        this.drag._startEvent(e.clientX);
    }

    _mouseMove(e){
        
        if(this.zoom.isZooming) return;
        
        this.drag.mouseMove(e);
    }

    _mouseUp(){
        this.drag._endEvent();
    }
    //#endregion



    //#region Touch interactions
    _touchStart(e) {
        const touches = e.changedTouches;
        this.drag._startEvent(touches[0].clientX);
    }

    _touchMove(e) {
        if(this.zoom.isZooming) return;
        this.drag.touchMove(e);
    }

    _touchEnd() {
        this.drag._endEvent();
    }
    //#endregion

}






class Drag {
    /**
     * control dragging events
     * @param {HTMLCanvasElement} rect - canvas events capturer
     */
     constructor(rect) {
        this.rect = rect;

        this._mobileSensitivity = 2;
        this.sensitivity = 20;

        this.dragWait = 16.66;
        this.maxDrawCall = 6;

        this.isDragging = false;
        this.reverse = true;
        this.canDrag = true;

        this.prevX = 0;
        this._bounding;
        //this.init();
    }

    /**
     * get the canvas bounding rect
     */
    get _bounding(){
        return this.rect.getBoundingClientRect();
    }

    init() {

        // mouse events
        this.rect.onmousedown = e => {
            this.mouseDown(e);
        }

        this.rect.onmousemove = e => {
            this.mouseMove(e);
        }

        this.rect.onmouseup = () => {
            this.mouseUp();
        }


        //touch events
        this.rect.addEventListener("touchstart" , (e) => {
            this.touchStart(e);
        });

        this.rect.addEventListener("touchmove" , (e) => {
            this.touchMove(e);
        });

        this.rect.addEventListener("touchend" , () => {
            this.touchEnd();
        });
        
    }


    //#region Mouse interactions
    mouseDown(e){
        this._startEvent(e.clientX);
    }

    mouseMove(e){
        
        if(!this.isDragging || !this.canDrag) return;
        
        this._pointerRotate(e);
    }

    mouseUp(){
        this._endEvent();
    }
    //#endregion




    //#region Touch interactions
    touchStart(e) {
        const touches = e.changedTouches;
        this._startEvent(touches[0].clientX);
    }

    touchMove(e) {
        //e.preventDefault();
        if(!this.isDragging || !this.canDrag) return;
        const touches = e.changedTouches;
        if(!touches.length > 1) return;
        this._touchRotate(touches[0].clientX);
    }

    touchEnd() {
        this._endEvent();
    }
    //#endregion


    //#region Drag setup
    _startEvent (clientX){
        this.isDragging = true;
        this.prevX = this._getCurrentX(clientX);
    }
    _endEvent (){
        this.isDragging = false;
    }
    //#endregion
    
    
    /**
     * animate when dragging using touch
     * @param {Number} clientX - touch X position
     */
    _touchRotate(clientX){
        //get the current position
        const currentX = this._getCurrentX(clientX);
        //calculate the difference between the last pos and the current pos
        const diff =  Math.round(currentX - this.prevX);
        
        //compare the absolute difference and the mobile sensibility
        if(Math.abs(diff) > this._mobileSensitivity) {

            //check if direction is reversed
            this.reverse = diff > 0;

            /**
             * bind the OnDrag 
             * @param {Boolean} detail - bool indicating drag direction 
             */
            this.rect.dispatchEvent(new CustomEvent("OnDrag", {
                bubbles: false,
                detail : this.reverse
            }));

            //save the current x position
            this.prevX = currentX;
        }
        
    }

    /**
     * animate when dragging using mouse
     * @param {Event} e - mouse event object
     */
    _pointerRotate(e){
        
        //get the absolut movement X (like the deltaX)
        const absMov = Math.abs(e.movementX);
        //get the current x of the mouse
        const currentX = this._getCurrentX(e.clientX);
        //calculate the difference between the last pos and the current pos
        const diff = Math.round(currentX - this.prevX);
        
        //compare the absolute difference and the mouse sensibility
        if(Math.abs(diff) > this.sensitivity) {
            //check if direction is reversed
            this.reverse = diff > 0;
            /**
             * bind the OnDrag 
             * @param {Boolean} detail - bool indicating drag direction 
             */
            this.rect.dispatchEvent(new CustomEvent("OnDrag", {
                bubbles: false,
                detail : this.reverse
            }));

            //disable dragging until saved frames when dragging finish animating
            this.canDrag = false

            /**
             * here we play animation based on the delta x mouvement,
             * we await for a certain amount of time for performances,
             * then we drag the next iteration
             * once finish, we should enable dragging
             */
            for (let i = 0; i < absMov % this.maxDrawCall; i++) {
                /* this.rect.dispatchEvent(new CustomEvent("OnDrag", {
                    bubbles: false,
                    detail : this.reverse
                })); */
                window.requestAnimationFrame(async () => await this._wait());
                this.rect.dispatchEvent(new CustomEvent("OnDrag", {
                    bubbles: false,
                    detail : this.reverse
                }));
            }
            //enable dragging
            this.canDrag = true;
            //save the current pos
            this.prevX = currentX;
        }
    }

    /**
     * return the valid click or touch 
     * @param {Number} clienX the clienX of the interaction
     * @returns {Number} valid click/touch in the rect
     */
    _getCurrentX(clienX) {
        return clienX - this._bounding.left;
    }

    /**
     * timer with promise
     * @returns {Promise} the wait promise
     */
    _wait() {
        const p = new Promise( (resolve) => {
            const timer = setTimeout( () => {
                resolve();
                clearTimeout(timer);
            } , this.dragWait);
        } );
        
        return p;
    }

}









class Zoom {

    constructor(rect , options){
        this.rect = rect;
        this.options = options;

        //cache touch event
        this.evCache = [];
        this.prevDiff = -1;

        this.isZooming = false;

        this.init();
    }

    init() {

        this.defaultOptions = {
            scaleFactor : 0.02,
            currentScale : 0.3,
            minScale : 0.1,
            maxScale : 1
        };
        this.setOptions();
        //this.wheelAndPinch();
        //this.pinchZoomEvent();
    }

    /**
     * set the zoom options
     * @param {Object} options - zoom options
     * @param {Number} options.scaleFactor 
     * @param {Number} options.currentScale
     * @param {Number} options.minScale 
     * @param {Number} options.maxScale
     */
    setOptions(options) {

        if (typeof(options) == "undefined") {

            this.options = this.defaultOptions;

        }else {
            Object.keys(options).forEach((opt) => {

                if(typeof(this.options[opt]) != "undefined") {
                    this.options[opt] = options[opt]
                    console.log(this.options[opt]);
                }
                
            })
        }

        this.currentScale = this.options.currentScale;
        this.scaleFactor = this.options.scaleFactor;
        this.minScale = this.options.minScale;
        this.maxScale = this.options.maxScale;

    }
    
    
    /**
     * zoom control events
     */
    wheelAndPinch(){
        this.rect.addEventListener('wheel', (e) => {
            e.preventDefault();
            this._wheelEvent(e);
        });   
    }


    pinchZoomEvent(){
        //touch events
        this.rect.addEventListener("pointerdown" , (e) => {
            this.touchStart(e);
        });

        this.rect.addEventListener("pointermove" , (e) => {
            this.touchMove(e);
        });

        this.rect.addEventListener("pointerup" , (e) => {
            this.touchEnd(e);
        });
    }


    //#region Touch interactions
    touchStart(e) {
        this.evCache.push(e);
    }

    touchMove(e) {

        // Find this event in the cache and update its record with this event
        const index = this.evCache.findIndex((cachedEv) => cachedEv.pointerId === e.pointerId);
        this.evCache[index] = e;

        // If two pointers are down, check for pinch gestures
        if (this.evCache.length === 2) {
            e.preventDefault();
            // Calculate the distance between the two pointers
            const curDiff = Math.abs(this.evCache[0].clientX - this.evCache[1].clientX);
            console.log(curDiff)
            if (this.prevDiff > 0) {
                this._pinchDetected(curDiff > this.prevDiff);
            }
            // Cache the distance for the next move event
            this.prevDiff = curDiff;
            
        } 
    }

    touchEnd(e) {
        this.isZooming = false;
        // Remove this pointer from the cache
        this._removeEvent(e);
        // If the number of pointers down is less than two then reset diff tracker
        if (this.evCache.length < 2) {
            this.prevDiff = -1;
        }
    }

    _removeEvent(e) {
        // Remove this event from the target's cache
        const index = this.evCache.findIndex((cachedEv) => cachedEv.pointerId === e.pointerId);
        this.evCache.splice(index, 1);
      }
    //#endregion

    
    _pinchDetected(ZoomIn){

        if(!this._zoom(ZoomIn)) return;
        this.isZooming = true;
        this._limitScale();

        this.rect.dispatchEvent(new CustomEvent("OnZoom", {
            bubbles: false
        }));
    }

    _wheelEvent(e){

        if(!this._zoom(e.deltaY < 0)) return;

        this._limitScale();

        this.rect.dispatchEvent(new CustomEvent("OnZoom", {
            bubbles: false
        }));
        
    }

    _zoom(zoomIn) {
        const timestamp = Date.now();
        if (this.zoomTimeStart === undefined) {
            this.zoomTimeStart = timestamp;
        }
        const elapsed = timestamp - this.zoomTimeStart;
    
        if(elapsed <= 16.66) { //framerate simulation must refactor 
            return false;
            
        }
        this.zoomTimeStart = timestamp;
        
        if(zoomIn) {

            //zoom in
            if(this.currentScale >= this.maxScale) return false
            this.currentScale += this.scaleFactor;
        }else{
            //zoomOut
            if(this.currentScale <= this.minScale) return false;
            this.currentScale -= this.scaleFactor;
        }

        return true;
    }



    _limitScale() {
        if(this.scaleFactor > this.maxScale || this.scaleFactor < this.minScale) {
            this.currentScale = Math.min(Math.max(this.currentScale, this.minScale), this.maxScale);
        }
        
    }
    
    
    
      
      

}


export {InputSystem}