
class Animation{

    /**
     * @constructor
     * @param {String} fileNameTemplate name of animation images , $ is replaced with id
     * @param {Number} countSprite total of frames
     * @param {Function} onFinishLoading callback when animation is loaded , return error if not loaded
     */
    constructor(fileNameTemplate , countSprite , onFinishLoading) {
        this._images = [];
        this._countSprite = countSprite;

        /**
         * the current frame index
         * @public
         */
        this.currentIndex = 0;

        this._loadAnimation(fileNameTemplate , onFinishLoading);
    }

    /**
     * 
     * @param {String} fileNameTemplate
     * @param {Function} onFinishLoading 
     * @private
     */
    _loadAnimation(fileNameTemplate , onFinishLoading) {
        let allPromises = [];
        for (let i = 0; i < this._countSprite; i++) {
            
            const p = new Promise( (resolve , reject) => {
                // replace ? with image id
                const reg = i.toLocaleString('en-US', {minimumIntegerDigits: 1, useGrouping:false});
                const path = fileNameTemplate.replace('?' , reg);
    
                // require and load the image
                const imgPath = require('@/' + path);
                const image = new Image();
                image.src = imgPath;
                this._images.push(image);
                //console.log(this.images[i]);
                image.addEventListener('load' , () => {
                    resolve();
                } , {once:true});

                image.addEventListener('error' , (e) => {
                    console.log(e);
                    this.errors.push(e);
                    reject(e);
                    return;
                });
            });

            //add the promise to the stack
            allPromises.push(p);
        }

        // using Promise.all ensure the frames Multi-thread loaded
        Promise.all(allPromises).then(() => {
            onFinishLoading();
        }).catch( (e) => {
            onFinishLoading(e);
        } )
    }


    getCurrentFrame() {
        return this._images[this.currentIndex];
    }

    setCurrentFrame(index) {
        this.currentIndex = index;
    }

    next(){
        
        this.currentIndex++;
        if (this.currentIndex >= this._countSprite) {
            this.currentIndex = 0;
        }
    }

    prev(){
        this.currentIndex--;
        if (this.currentIndex < 0) {
            this.currentIndex = this._countSprite-1;
        }
    }

}


export { Animation };