class Resizer {

    constructor (canvasObj) {

        this.canvasObj = canvasObj;
        this.init();
    }

    init(){
        this.resize();
    }

    resize(){

        for (const [key=key, rect] of Object.entries(this.canvasObj)) {
            rect.width = window.innerWidth;
            rect.height = window.innerHeight;
        }
        
    }
}

export {Resizer}