<template>
    <div class="filter position-absolute top-50 start-50 translate-middle">
        <div class="d-flex justify-content-center">

            <div id="filter" class="toast" role="alert" aria-live="assertive" aria-atomic="true"
                data-bs-autohide="false">
                <div class="toast-header">
                    <strong class="me-auto">Filtre</strong>
                    <small>Fermer</small>
                    <button type="button" class="btn-close" @click="closeToast"></button>
                </div>
                <div class="toast-body">
                    <div class="slider">

                        <div class="fs-6 h5 text-center">
                            <span>Surface en m</span>
                        </div>

                        <div class="position-relative ranges">
                            <input type="range" class="form-range position-absolute" id="minRange" :max="max" :min="min"
                                :step="step" v-model="minValue" @input="controlSlider(true)">
                            <input type="range" class="form-range position-absolute" id="maxRange" :max="max" :min="min"
                                :step="step" v-model="maxValue" @input="controlSlider(false)">
                        </div>

                    </div>

                    <div class="range-info d-flex justify-content-between my-4">

                        <div class="form-floating me-2">
                            <input type="number" id="minInput" class="form-control" v-model="minValue"
                                @input="controlSlider(true)">
                            <label for="minInput">surface minimale</label>
                        </div>
                        <div class="form-floating">
                            <input class="form-control" type="number" id="minInput" v-model="maxValue"
                                @input="controlSlider(false)">
                            <label for="maxInput">surface maximale</label>
                        </div>

                    </div>

                    <div class="f-options d-flex">
                        <button class="btn btn-success btn-sm">T2</button>
                        <button class="btn btn-warning btn-sm">T3</button>
                        <button class="btn btn-primary btn-sm">T4</button>
                    </div>
                </div>
            </div>
        </div>
    </div>

</template>


<script>

import { Toast } from "bootstrap"

export default {
    name: "FilterComponent",

    props: {
        showToast: Boolean,
        min: {
            type: Number,
            default: 0
        },
        max: {
            type: Number,
            default: 100
        },
        step: {
            type: Number,
            default: 1
        },
        gap: {
            type: Number,
            default: 10
        },
        sliderColor: {
            type: String,
            default: "#cecece"
        },
        rangeColor: {
            type: String,
            default: "#21af4f"
        }
    },

    data() {
        return {
            toast: null,

            appReady: false,
            minValue: this.min,
            maxValue: this.max,
            surface: {
                min: this.min,
                max: this.max,
            },

            maxSlider: null,
        }
    },

    methods: {

        initSliders() {
            this.maxSlider = document.getElementById("maxRange");
            this.fillSlider(this.minValue, this.maxValue, this.maxSlider);
        },

        initToast() {
            const filterToast = document.getElementById("filter");
            this.toast = new Toast(filterToast);
            this.toogleToast();
        },
        toogleToast() {
            if (this.showToast) {
                this.toast.show();
            } else {
                this.toast.hide();
            }
        },

        closeToast() {
            this.$emit("onCloseToast");
        },


        controlSlider(isMinSlider) {
            const gap = Number(this.gap);
            const min = Number(this.minValue);
            const max = Number(this.maxValue);
            const validMax = max - gap;

            if (min < this.min) {
                this.minValue = this.min;
                return;
            }

            if (max > this.max) {
                this.maxValue = this.max;
                return;
            }

            if (isMinSlider) {
                if (min > validMax) {
                    this.minValue = validMax;
                    this.surface.min = validMax;
                    this.fillSlider(validMax, max, this.maxSlider);
                    return;
                }
                this.fillSlider(min, max, this.maxSlider);
                this.surface.min = min;
                return;
            }

            if (this.minValue > validMax) {
                this.maxValue = min + gap;
                this.surface.max = min + gap;
                //console.log(min + gap)
                this.fillSlider(min, (min + gap), this.maxSlider);
                return;
            }
            this.surface.max = max;
            this.fillSlider(min, max, this.maxSlider);

        },

        fillSlider(from, to, controlSlider) {
            const rangeDistance = this.max - this.min;
            const fromPosition = from - this.min;
            const toPosition = to - this.min;
            const sliderColor = this.sliderColor;
            const rangeColor = this.rangeColor;

            controlSlider.style.background = `linear-gradient(
            to right,
            ${sliderColor} 0%,
            ${sliderColor} ${(fromPosition) / (rangeDistance) * 100}%,
            ${rangeColor} ${((fromPosition) / (rangeDistance)) * 100}%,
            ${rangeColor} ${(toPosition) / (rangeDistance) * 100}%, 
            ${sliderColor} ${(toPosition) / (rangeDistance) * 100}%, 
            ${sliderColor} 100%)`;
        },

        surfaceChanged() {
            if (this.appReady) {
                this.$emit("onSurfaceChanged", this.surface);
            }
        }
    },

    watch: {
        "showToast": function () {
            this.toogleToast();
        },
        "surface.min": function () {
            this.surfaceChanged();
        },
        "surface.max": function () {
            this.surfaceChanged();
        }
    },

    mounted() {
        this.initToast();
        this.initSliders();

        window.addEventListener("OnAppReady", () => {
            this.appReady = true;
            this.surfaceChanged();
        }, { once: true })
    }
}

</script>


<style scoped>
.filter {
    z-index: 101;
    max-width: 60%;
    width: auto;
}

.ranges {
    margin: 2em 0;
    min-height: 1em;
}

input[type=range] {
    height: 0.3em;
    width: 100%;
    position: absolute;
    background-color: #C6C6C6;
    pointer-events: none;
    border-radius: 2em;
}

input[type=range]::-webkit-slider-thumb {
    cursor: pointer;
    pointer-events: all;
}

input[type=range]::-webkit-slider-runnable-track {
    background-color: transparent;

}

#minRange {
    top: 0.15em;
    height: 0;
    z-index: 1;
}

.btn {
    margin: 0 .2em;
}
</style>