class Img{

    constructor(path){
        this.path = path;
        this.source = null;
        this.load();
    }

    load () {

        this.source = new Image();

        this.source.onload = () => {
            this.source.dispatchEvent(new CustomEvent("OnLoad", {
                bubbles: false
            }));
        }
        this.source.onerror = (e) => {
            this.source.dispatchEvent(new CustomEvent("OnError", {
                bubbles: false,
                error : e
            }));
        }

        this.source.src = this.path;
    }
}


export{ Img };