class TilesController {

    /**
     * 
     * @param {Object} breakpoints - contain sizes for all breakpoints , keys expected : { small , medium , big }
     * @param {Object} naturalSizes - Original sizes of images , expected keys : {maxWidth : {w , h} , big , medium}
     * @param {Number} cropSize - the tile size of the splitted image (e.g 512x512)
     * @param {Number} maxCache - the max cache of tiles
     *  
     */

    constructor (breakpoints , naturalSizes , cropSize , maxCache) {

        
        if(typeof breakpoints !== "undefined" && typeof breakpoints === "object") {
            this.breakPoints = breakpoints;
        }else{
            alert("breakpoints not valid")
            throw new Error("breakpoints not valid");
        }
        
        /**
         * the preloaded size by default (this is loaded when the page load)
         */

        this.preloadedBP = breakpoints.small;

        /**
         * Original images sizes
         */

        this.naturalSizes = naturalSizes;

        this.cropSize = cropSize;
        this.maxCache = maxCache;


        /**
         * the current natural image width ,
         * when loading tiles we must know the original width of the current breakpoint
         * we must calculate height based on width with the right aspect ratio
         */
        
        this._currentNaturalWidth = null;

        this._currentFrame = 0;
        this._currentBreakPoint;
        this._currentScaleFactor;

        /** 
         * the cache array , contain lazy-loaded images width additional info
         * */

        this.preloadedFrames = new Array();


        this._timeOutLoad;

        this.init();
    }

    init() {
    }


    /**
     * 
     * @param {Number} currentWidth - the current image width 
     * @param {Number} frameIndex - the current frame index 
     * @param {Number} scaleFactor 
     * @returns resolve with loaded tiles || reject
     */
    fetchTiles(currentWidth , frameIndex , scaleFactor) {

        this._setCurrentBP(currentWidth);
        this._setCurrentNaturalSize(this._currentBreakPoint);

        if(currentWidth <= this.preloadedBP) return this._loadTiles(true);

        
        this._currentFrame = frameIndex;
        this._currentScaleFactor = scaleFactor;


        if(!this.preloadedFrames.length) {
            //first fetch
            console.log("first fetch");

            return this._loadTiles();
        }


        const preloaded = this.preloadedFrames.find((v) => {
            if(v.frame === this._currentFrame && v.breakPoint === this._currentBreakPoint) {
                return v;
            }
        });

        if(typeof preloaded !== "undefined") {
            //console.log(preloaded);
            return this._loadExistingTiles(preloaded);
        }

        return this._loadTiles();
        
    }



    /**
     * load tiles from cache
     * @param {Object} preloaded the cached tiles object if found
     * @returns the cached canvas containing tiles of the current frame 
     */
    _loadExistingTiles(preloaded){
        
        return new Promise((resolve , reject) => {
            if(preloaded.frame === this._currentFrame && preloaded.breakPoint === this._currentBreakPoint) {
                resolve(preloaded.canvas);
            }else{
                reject();
            }
        });
    }

    /**
     * load tiles of current frame
     * @param {boolean} error this is helpful for rejecting the promise if frame is changed while loading tiles
     * @returns the loaded canvas containing tiles of the current frame 
     */
    _loadTiles(error = false) {
        window.clearTimeout(this._timeOutLoad);
        const p = new Promise((resolve , reject ) => {
            if(error) {
                reject();
            }else{
                this._timeOutLoad = window.setTimeout( () => {
                    const folder = this._getTilesFolder(this._currentFrame , this._currentBreakPoint);
                    const index = this._currentFrame;
                    const breakpoint = this._currentBreakPoint;
                    const scaleFactor = this._currentScaleFactor;
                    this._createAndLoadTiles(folder,  (WSCanvas) => {
    
                        const tilesInfo = {
                            frame : index,
                            breakPoint : breakpoint,
                            canvas : WSCanvas
                        };
    
                        this._addToCache(tilesInfo);
    
                        if(!this._canLoadImg(index , breakpoint , scaleFactor)) {
                            reject();
                            //window.clearTimeout(this._timeOutLoad);
                            return;
                        }
                        resolve(WSCanvas);
                        //window.clearTimeout(this._timeOutLoad);
                    });
                } , 16.66);
                
            }
            
        });
        return p;
    }

    /**
     * add loaded tiles if not already exist into the cache 
     * @param {Object} cacheInfo info about the cached canvas 
     */
    _addToCache(cacheInfo) {
        
        if(this.preloadedFrames.includes(cacheInfo)) return;

        this._checkCache();
        this.preloadedFrames.push(cacheInfo);
    }
    

    /**
     * load tiles method , this is multi-thread by using the Promise all
     * @param {String} folder the tiles folder
     * @param {Function} onFinish calllback when finish loading tiles
     */
    _createAndLoadTiles(folder , onFinish) {

        const natWidth = this._currentNaturalWidth;
        const natHeight = this._calculateWithAspectRatio(this._currentNaturalWidth);

        let canvas = document.createElement('canvas');
        let ctx = canvas.getContext("2d");


        canvas.width = natWidth;
        canvas.height = natHeight;
        
        const cropSize = this.cropSize; // the crop rect size

        //console.log(this._currentNaturalWidth);
        
        const partsX = Math.floor(natWidth / cropSize);
        const partsY = Math.floor(natHeight / cropSize);

        const moduloX = natWidth % cropSize;
        const moduloY = natHeight % cropSize;

        const lengthX = moduloX != 0 ? partsX+1 : partsX;
        const lengthY = moduloY != 0 ? partsY+1 : partsY;

        let allPromises = new Array();

        for(let y= 0 ; y < lengthY ; y++) {
            for(let x = 0 ; x < lengthX ; x++ ) {

                const p = new Promise((resolve , reject) => {
                    const sx = x*cropSize , 
                    sy = y*cropSize;
                    
                    const index = lengthX * y + x;
                    const src = `${folder}p-${index}.jpg` ;
                    const path = require("@/" + src);
    
                    const img = new Image();
                    img.src = path;
                    img.onload = () => {
                        ctx.drawImage(img, sx, sy);
                        resolve();
                    };

                    img.onerror = () => {
                        reject();
                    }
                    
                    
                });

                allPromises.push(p);

            }
        }

        Promise.all(allPromises).then(() => {
            onFinish(canvas);
        })
    }

    /**
     * if loaded tiles is the same after the fetch
     * @param {Number} frame - the frame when tiles loaded
     * @param {Number} breakPoint 
     * @param {Number} scaleFactor 
     * @returns 
     */
    _canLoadImg(frame , breakPoint , scaleFactor) {

        if(
            frame !== this._currentFrame || 
            breakPoint !== this._currentBreakPoint || 
            scaleFactor !== this._currentScaleFactor
        )
        {
            return false;
        }
        return true;
    }

    
    /**
     * 
     * @param {Context} ctx 
     * @param {CanvasImageSource} img 
     * @param {Number} sx - x source coordinate
     * @param {Number} sy - y source coordinate
     */
    _draw(ctx , img , sx , sy) {
        ctx.clearRect(sx, sy, ctx.canvas.width, ctx.canvas.height);
        ctx.drawImage(img, sx, sy);
    }


    /**
     * return the folder path depending on given breakpoint
     * @param {Number} frameIndex 
     * @param {Number} breakpoint 
     * @returns  tiles folder
     */
    _getTilesFolder(frameIndex , breakpoint){

        let folder = "assets/tiles";
        switch (breakpoint) {
            case this.breakPoints.medium:
                folder += "/medium-cropped/00_" + frameIndex;
                break;
            case this.breakPoints.big:
                folder += "/big-cropped/00_" + frameIndex;
                break;
        
            default:
                break;
        }
        return folder + "/";
    }


    /**
     * set the current original size based on the given breakpoint
     * @param {Number} breakpoint 
     */
    _setCurrentNaturalSize(breakpoint) {
        switch (breakpoint) {
            case this.breakPoints.medium:
                this._currentNaturalWidth = this.naturalSizes.medium;
                break;
            case this.breakPoints.big:
                this._currentNaturalWidth = this.naturalSizes.big;
                break;
        
            default:
                break;
        }
    }

    /**
     * set current breakpoint based on given width
     * @param {Number} width 
     */
    _setCurrentBP(width){

        Object.values(this.breakPoints).forEach(bp => {
            if(bp >= width) {
                this._currentBreakPoint = bp;
                return;
            }
        });

    }

    /**
     * check if cache is out of max cache , if so then we delete the first element of the cache array
     */
    _checkCache(){
        if(this.preloadedFrames.length > this.maxCache) {
            this.preloadedFrames.shift();
            console.log("removed from array");
        }
    }


    /**
     * calculate the height of image based on width preserving the aspect ratio
     * @param {Number} width 
     * @returns the calculated height
     */
    _calculateWithAspectRatio(width) {
        const aspect = this.naturalSizes.maxSize.w / this.naturalSizes.maxSize.h;
        const height = width / aspect;

        return height;
    }


    
}


export {TilesController}