<template>

    <div v-show="showPopover" class="position-absolute" id="popper">
        <div class="card shadow border border-0">
            <div class="card-header">
                <h3 class="h6 lead fs-6">{{ infoHt.nom }}</h3>
            </div>
            <div class="card-body">
                <span>{{ infoHt.statut }}</span>
                <p>Surface : {{ infoHt.surface }} m2</p>
            </div>
        </div>
    </div>

</template>


<script>

import { createPopper } from '@popperjs/core';
import REGIONS from '@/assets/regions.json'

export default {
    name: 'PopoverComponent',

    props: {

    },

    data() {
        return {
            showPopover: false,

            pInstance: null,

            popper: null,

            virtualElement: null,

            infoHt: {}
        }
    },

    methods: {

        setPopperTitle(title) {
            this.titleHt = title;
        },

        setPopperBody(region) {
            this.infoHt = region;
            //console.log(this.infoHt);
        },

        initPopover() {

            this.virtualElement = {
                getBoundingClientRect: this.generateGetBoundingClientRect(),
            };

            this.popper = document.getElementById('popper');

            this.pInstance = createPopper(this.virtualElement, this.popper, {
                placement: 'top',
                modifiers: [
                    {
                        name: 'offset',
                        options: {
                            offset: ({ placement }) => {
                                if (placement === 'bottom') {

                                    return [0, 30];
                                }

                                if (placement === 'top') {

                                    return [0, 15];
                                }

                                return [];
                            },
                        }
                    },
                ]
            });
        },


        generateGetBoundingClientRect(x = 0, y = 0) {
            return () => ({
                width: 0,
                height: 0,
                top: y,
                right: x,
                bottom: y,
                left: x,
            });
        },

        updatePopover(x, y) {

            this.virtualElement.getBoundingClientRect = this.generateGetBoundingClientRect(x, y);
            this.pInstance.update();
        }
    },

    mounted() {

        this.initPopover();

        window.addEventListener("OnHSHover", (e) => {

            const detail = e.detail;
            const region = detail.region;
            if (region == undefined) return;

            this.showPopover = true;

            const pos = e.detail.mousePos;
            this.updatePopover(pos.x, pos.y);


            const regionBody = REGIONS[region.name];

            //console.log(regionBody);
            this.setPopperTitle(region.name);
            this.setPopperBody(regionBody);

        });

        window.addEventListener("OnHSHoverOut", () => {
            this.showPopover = false;

        });

    },



}

</script>




<style scoped>
#popper {
    z-index: 4;
}

.card {
    border-radius: 0;
}
</style>